<template>
  <section class="container" id="portfolio" :style="getScreenHeight">
    <div class="mb-4 text-center section-title">
      &lt;{{ $t("portfolio") }}&nbsp; &frasl;&gt;
    </div>
    <ul class="d-flex justify-content-center mb-3" style="flex-wrap: nowrap">
      <li
        :class="['tab-item mx-md-2', currentTab == '#pills-1' ? 'active' : '']"
        tab-target="#pills-1"
        @click="showWaterFall"
      >
        {{ $t("artillustration") }}
      </li>

      <li
        :class="['tab-item mx-md-2', currentTab == '#pills-2' ? 'active' : '']"
        tab-target="#pills-2"
        @click="showWaterFall"
      >
        {{ $t("productrender") }}
      </li>

      <li
        :class="['tab-item mx-md-2', currentTab == '#pills-3' ? 'active' : '']"
        tab-target="#pills-3"
        @click="showWaterFall"
      >
        {{ $t("uiuxdesign") }}
      </li>
    </ul>

    <div class="tab-content mb-4">
      <div v-show="currentTab == '#pills-1'" id="pills-1">
        <div class="waterfall">
          <div class="item">
            <div style="width: 100%; height: 200px; background: red"></div>
          </div>
          <div class="item">
            <div style="width: 100%; height: 100px; background: red"></div>
          </div>
          <div class="item">
            <div style="width: 100%; height: 50px; background: red"></div>
          </div>
        </div>
      </div>

      <div v-show="currentTab == '#pills-2'" id="pills-2">
        <div class="waterfall">
          <div class="item">
            <div style="width: 100%; height: 200px; background: red"></div>
          </div>
          <div class="item">
            <div style="width: 100%; height: 220px; background: red"></div>
          </div>
          <div class="item">
            <div style="width: 100%; height: 50px; background: red"></div>
          </div>
        </div>
      </div>
      <div v-show="currentTab == '#pills-3'" id="pills-3">
        <div class="waterfall">
          <div class="item">
            <div style="width: 100%; height: 50px; background: red"></div>
          </div>
          <div class="item">
